interface ContentfulFetcherProps {
  contentType: string;
  children?: ReactNode;
  className?: string;
  limit?: number;
  order?: string;
  filterField?: string;
  searchOperator?: string;
  filterValue?: string;
  noAutoRepeat?: boolean;
  noLayout?: boolean;
  setControlContextData?: (data: {
    types?: { name: string; id: string }[];
    fields?: string[];
  }) => void;
}

export const ContentfulFetcherMeta: ComponentMeta<ContentfulFetcherProps> = {
  name: "ContentfulFetcher",
  displayName: "Contentful Fetcher",
  importName: "ContentfulFetcher",
  importPath: modulePath,
  providesData: true,
  description:
    "Fetches Contentful data and repeats content of children once for every row fetched. ",
  defaultStyles: {
    display: "grid",
    gridTemplateColumns: "1fr 1fr 1fr 1fr",
    gridRowGap: "8px",
    gridColumnGap: "8px",
    padding: "8px",
    maxWidth: "100%",
  },
  props: {
    children: {
      type: "slot",
      defaultValue: {
        type: "vbox",
        styles: {
          padding: "8px",
        },
        children: {
          type: "component",
          name: "ContentfulField",
        },
      },
    },
    contentType: {
      type: "choice",
      options: (props, ctx) =>
        ctx?.types?.map((type: any) => ({
          label: type?.name,
          value: type?.sys?.id,
        })) ?? [],
      displayName: "Content type",
      description: "Content type to be queried.",
    },
    limit: {
      type: "number",
      displayName: "Limit",
      description: "Limit the number of entries that are returned.",
    },
    filterField: {
      type: "choice",
      displayName: "Filter field",
      description: "Field (from Collection) to filter by",
      options: (props, ctx) => ctx?.fields ?? [],
      hidden: (props, ctx) =>
        props.contentType == undefined || props.limit == undefined,
    },
    searchOperator: {
      type: "choice",
      displayName: "Query Operator",
      description:
        "Search Operator filter by.Read more (https://www.contentful.com/developers/docs/references/content-delivery-api/#/reference/search-parameters/)",
      options: (props, ctx) => {
        return searchOperators.map((item: any) => ({
          label: item?.label,
          value: item?.value,
        }));
      },
      hidden: (props, ctx) => !props.filterField,
    },
    filterValue: {
      type: "string",
      displayName: "Filter value",
      description: "Value to filter by, should be of filter field type",
      hidden: (props, ctx) => !props.searchOperator,
    },

    order: {
      type: "string",
      displayName: "Order",
      description: "Order entries with a specific attribute.",
    },
    noAutoRepeat: {
      type: "boolean",
      displayName: "No auto-repeat",
      description: "Do not automatically repeat children for every entry.",
      defaultValue: false,
    },
    noLayout: {
      type: "boolean",
      displayName: "No layout",
      description:
        "When set, Contentful Fetcher will not layout its children; instead, the layout set on its parent element will be used. Useful if you want to set flex gap or control container tag type.",
      defaultValue: false,
    },
  },
};

export function ContentfulFetcher({
  filterField,
  filterValue,
  searchOperator,
  noAutoRepeat,
  contentType,
  children,
  className,
  limit,
  noLayout,
  order,
  setControlContextData,
}: ContentfulFetcherProps) {
  const creds = ensure(useContext(CredentialsContext));
  const cacheKey = JSON.stringify({
    filterField,
    filterValue,
    searchOperator,
    creds,
  });

  const url = "https://cdn.contentful.com";

  const { data: contentTypes } = usePlasmicQueryData<any | null>(
    `${cacheKey}/contentTypes`,
    async () => {
      const resp = await fetch(
        `${url}/spaces/${creds.space}/environments/${creds.environment}/content_types?access_token=${creds.accessToken}`
      );
      return resp.json();
    }
  );

  setControlContextData?.({
    types: contentTypes?.items ?? [],
  });

  const { data: entriesData } = usePlasmicQueryData<any | null>(
    contentType && limit ? `${cacheKey}/${contentType}/entriesData` : null,
    async () => {
      let query;
      if (order) {
        query = `/spaces/${creds.space}/environments/${creds.environment}/entries?access_token=${creds.accessToken}&content_type=${contentType}&order=${order}&limit=${limit}`;
      } else {
        query = `/spaces/${creds.space}/environments/${creds.environment}/entries?access_token=${creds.accessToken}&content_type=${contentType}&limit=${limit}`;
      }
      const resp = await fetch(`${url}${query}`);
      return resp.json();
    }
  );

  const { data: filteredData } = usePlasmicQueryData<any | null>(
    contentType && filterField && filterValue
      ? `${cacheKey}/${contentType}/filteredData`
      : null,
    async () => {
      const queryPath = `/spaces/${creds.space}/environments/${creds.environment}/entries?access_token=${creds.accessToken}&content_type=${contentType}&fields.${filterField}${searchOperator}=${filterValue}&limit=${limit}`;

      const resp = await fetch(`${url}${queryPath}`);
      return resp.json();
    }
  );

  if (!creds.space || !creds.accessToken) {
    return (
      <div>
        Please specify a valid API Credentials: Space, Access Token and
        Environment
      </div>
    );
  }

  if (!contentType && !limit) {
    return <div>Please select a content type and specify the limit</div>;
  }
  if (contentType && !limit) {
    return <div> Please specify the limit</div>;
  }

  if (!entriesData) {
    return <div>Please select a content type and and enter the limit</div>;
  }

  const filterFields: string[] = entriesData?.items.flatMap((item: any) => {
    const fields = Object.keys(item.fields).filter((field) => {
      const value = get(item, field);
      return typeof value !== "object" && field !== "photos";
    });
    console.log(fields);
    return fields;
  });

  setControlContextData?.({
    types: contentTypes?.items ?? [],
    fields: uniq(filterFields ?? []),
  });

  if (searchOperator && !filterValue && !filterField) {
    return <div>Please specify a Filter Field and a Filter Value</div>;
  }
  if (!searchOperator && filterValue && !filterField) {
    return <div>Please specify a Search Operator and a Filter Field</div>;
  }
  if (!searchOperator && !filterValue && filterField) {
    return <div>Please specify a Search Operator and a Filter Value</div>;
  }

  if (searchOperator && filterValue && !filterField) {
    return <div>Please specify a Filter Field</div>;
  }
  if (!searchOperator && filterValue && filterField) {
    return <div>Please specify a Search Operator</div>;
  }
  if (searchOperator && !filterValue && filterField) {
    return <div>Please specify a Filter Value</div>;
  }

  let renderedData;

  if (filteredData) {
    if (filteredData?.items?.length === 0) {
      return <div className={className}>No published entry found</div>;
    }

    renderedData = filteredData?.items?.map((item: any, index: number) => (
      <DataProvider
        key={item?.sys?.id}
        name={"contentfulItem"}
        data={item}
        hidden={true}
      >
        <DataProvider name={makeDataProviderName(contentType)} data={item}>
          {repeatedElement(index, children)}
        </DataProvider>
      </DataProvider>
    ));
  } else {
    if (entriesData?.items?.length === 0) {
      return <div className={className}>{contentType} is empty</div>;
    }

    renderedData = entriesData?.items?.map((item: any, index: number) => (
      <DataProvider
        key={item?.sys?.id}
        name={"contentfulItem"}
        data={item}
        hidden={true}
      >
        <DataProvider name={makeDataProviderName(contentType)} data={item}>
          {repeatedElement(index, children)}
        </DataProvider>
      </DataProvider>
    ));
  }

  return (
    <DataProvider name="contentfulItems" data={entriesData?.items}>
      {noAutoRepeat ? (
        children
      ) : noLayout ? (
        <> {renderedData} </>
      ) : (
        <div className={className}> {renderedData} </div>
      )}
    </DataProvider>
  );
}
interface ContentfulFieldProps {
  className?: string;
  objectPath?: (string | number)[];
  setControlContextData?: (data: { data: object }) => void;
}

export const ContentfulFieldMeta: ComponentMeta<ContentfulFieldProps> = {
  name: "ContentfulField",
  displayName: "Contentful Field",
  importName: "ContentfulField",
  importPath: modulePath,
  props: {
    objectPath: {
      type: "dataSelector",
      data: (props, ctx) => ctx?.data ?? {},
      displayName: "Field",
      description: "Field to be displayed.",
    },
  },
};

export function ContentfulField({
  className,
  objectPath,
  setControlContextData,
}: ContentfulFieldProps) {
  const item = useSelector("contentfulItem")?.fields;
  if (!item) {
    return <div>ContentfulField must be used within a ContentfulFetcher </div>;
  }
  setControlContextData?.({
    data: item,
  });
  if (!objectPath) {
    return <div>Please specify a valid path or select a field.</div>;
  }

  const data = get(item, objectPath);
  if (
    typeof data === "object" &&
    "nodeType" in data &&
    data.nodeType === "document"
  ) {
    return (
      <div
        className={className}
        dangerouslySetInnerHTML={{ __html: documentToHtmlString(data) }}
      />
    );
  } else if (
    typeof data === "object" &&
    "contentType" in data &&
    data.contentType.includes("image")
  ) {
    return <img className={className} src={data.url} />;
  } else if (!data) {
    return <div>Please specify a valid field.</div>;
  } else if (typeof data !== "object") {
    return <div className={className}>{data}</div>;
  } else {
    return <div className={className}>{data.toString()}</div>;
  }
}
